import React, { useState } from 'react';
import styles from './CardsQS.module.css';

const CardsQS = () => {
    const [flipped, setFlipped] = useState([false, false]);

    const handleFlip = (index) => {
        const newFlipped = [...flipped];
        newFlipped[index] = !newFlipped[index];
        setFlipped(newFlipped);
    };

    return (
        <div className={styles.wrapper}>
            <div className={styles.cols}>

                {/* Primer card (Misin) */}
                <div
                    className={`${styles.col} ${flipped[0] ? styles.hover : ''}`}
                    onClick={() => handleFlip(0)}
                >
                    <div className={styles.container}>
                        <div
                            className={styles.front}
                            style={{ backgroundImage: `url(/img/mision.jpg)` }}
                        >
                            <div className={styles.inner}>
                                <span>Misin</span>
                            </div>
                        </div>
                        <div className={styles.back}>
                            <div className={styles.inner}>
                                <p>Nuestra misin es clara: crear un espacio nico donde cada iniciativa, proyecto y esfuerzo dedicado a la proteccin y cuidado del ocano en Chile encuentre su lugar. Vivimos en un pas bendecido con vastas costas y una rica biodiversidad marina.</p>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Segundo card (Visin) */}
                <div
                    className={`${styles.col} ${flipped[1] ? styles.hover : ''}`}
                    onClick={() => handleFlip(1)}
                >
                    <div className={styles.container}>
                        <div
                            className={styles.front}
                            style={{ backgroundImage: `url(/img/vision.jpg)` }}
                        >
                            <div className={styles.inner}>
                                <span>Visin</span>
                            </div>
                        </div>
                        <div className={styles.back}>
                            <div className={styles.inner}>
                                <p>En Restart Ocean creemos que juntos podemos marcar la diferencia. Aqu, tanto empresas como individuos pueden compartir sus proyectos, intercambiar ideas y forjar alianzas que impulsen un cambio real y duradero.</p>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Tercer card (Propsito) */}
                <div
                    className={`${styles.col} ${flipped[1] ? styles.hover : ''}`}
                    onClick={() => handleFlip(1)}
                >
                    <div className={styles.container}>
                        <div
                            className={styles.front}
                            style={{ backgroundImage: `url(/img/proposito.jpg)` }}
                        >
                            <div className={styles.inner}>
                                <span>Propsito</span>
                            </div>
                        </div>
                        <div className={styles.back}>
                            <div className={styles.inner}>
                                <p>Nuestra plataforma est diseada para inspirar, conectar y movilizar a quienes aman y valoran el ocano, promoviendo acciones concretas que aseguren un futuro sostenible para nuestras aguas y las generaciones futuras.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CardsQS;